<template>
  <!-- 主页面 -->
  <div class="TtpodMain">
    <!-- 表头 -->
     
    <div class="header">
      <div class="header-icon-search">
        <router-link to="/seek">
        <van-icon name="search" class="search" />
        </router-link>
      </div>
          
      <div class="address">
        <!-- 点击路由跳转到所有城市页面 -->
        <span  @click="MsitegoHome()">{{geohash.address}}</span>
      </div>
      <router-link to="/mine">
      <div class="header-icon-user" v-if="this.$store.state.ma!=''">

        <i class="el-icon-user"></i>
      </div>
      </router-link>
      <router-link to="/login">
      <div class="header-icon-user" v-if="this.$store.state.ma==''">
          <span>登录</span>
          <span>|</span>
          <span>注册</span>
      </div>
      </router-link>
    </div>
    <!-- 轮播 -->
    <div class="mint-swipe">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item>
          <ul>
            <li v-for="(v,i) in info" :key="i" v-if="i<8">
              <img :src="`https://fuss10.elemecdn.com${v.image_url}`" />
              <span>{{v.title}}</span>
            </li>
          </ul>
        </van-swipe-item>
        <van-swipe-item>
          <ul>
            <li v-for="(v,i) in info" :key="i" v-if="i>=8&i<info.length">
              <img :src="`https://fuss10.elemecdn.com${v.image_url}`" />
              <span>{{v.title}}</span>
            </li>
          </ul>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="main">

      <!-- 小妞 -->
          <div class="img"  name="xian" v-if="img"><img src="./1.gif" alt=""></div>

      <!-- 附件商家 -->
      <div class="adjunct">
        <div class="adjunct-merchant">
          <van-icon name="shop-o" class="shop-o" />
          <span>附件商家</span>
        </div>
        <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
  



        <div class="main-content">
          <ul>
            <li v-for="(v,i) in arr" :key="i" @click="details(v)">
              <img :src="`//elm.cangdu.org/img/${v.image_path}`" alt />
              <div class="item">
                <div class="item-left">
                  <div class="item-left-top">
                    <span>品牌</span>
                    <p>{{v.name}}</p>
                  </div>
                  <div class="item-left-content">
                    <van-rate :value="v.rating" readonly size="0.12rem" gutter="0" />
                    <span>{{v.rating}}</span>
                    <p>月售{{v.recent_order_num}}单</p>
                  </div>
                  <div class="item-left-footer">
                    <!-- {{v.piecewise_agent_fee.tips}} -->
                    <span>￥{{v.float_minimum_order_amount}}起送/配送约5￥</span>
                  </div>
                </div>
                <div class="item-right">
                  <div class="item-right-top">
                    <span>
                      <em v-for="(l,o) in v.supports" :key="o">{{l.icon_name }}</em>
                    </span>
                  </div>
                  <div class="item-right-content">
                    <!-- {{v.delivery_mode.text}} -->
                    <span>蜂鸟专送</span>
                    <p>准时达</p>
                  </div>
                  <div class="item-right-footer">
                    <span>
                      {{v.distance}}/
                      <em>{{v.order_lead_time}}</em>
                    </span>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>


</van-list>

      </div>
    </div>
    <!--尾部 -->
            <Footer :Di=true></Footer>
  </div>
</template>

<script>
import Vue from 'vue';
import Footer from "./../Unify/Footer"
import { Swipe, SwipeItem } from 'vant';
Vue.use(Swipe).use(SwipeItem);
export default {
  data() {
    return {
      info: [],
      arr: [],
      geohash:"",
      img:true,
      loading: false,
      finished: false,
      limit:0,
      show:true

    };
  },
  components:{
          Footer
        },
  methods: {
    //路由跳转到所有城市页面
    MsitegoHome() {
      this.$router.push({ path: "/home" });
    },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        if(this.limit+=10){
          //附近商家数据内容
          this.$http.get(
              "http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit="+this.limit+"&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]="
            )
            .then(res => {
              // console.log(res.data);
              this.arr = res.data;
              // console.log(this.arr);
              this.img=false;
              
            });
        
        for (let i = 0; i < 10; i++) {
          this.arr.push(this.arr.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.arr.length >= 200) {
          this.finished = true;
        }
        }
      }, 500);
    },
    details(v){
      console.log(v);
      var hhh=v.latitude+','+v.longitude
      console.log(hhh);
      
      this.$router.push({path:"/shop",query:{geohash:hhh,id:v.id,img:v.image_path}})
      // alert(1111)
    }
  

  },
  mounted() {
    this.onLoad()
    // console.log(this.$store.ma);
    
    this.img=true;
    
    //轮播数据内容
    this.$http
      .get(
        "http://elm.cangdu.org/v2/index_entry?geohash="+this.geohash+"&group_type=1&flags[]=F"
      )
      .then(res => {
        // console.log(res);
        this.info = res.data;
        // this.img=false;

      });

    
     this.geohash=this.$store.state.dizhi;
    //  console.log(this.geohash);
      
      this.$http.get(
        "http://elm.cangdu.org/v2/pois/"+this.geohash+""
      )
      .then(res => {
        // console.log(res);
        this. geohash = res.data;

      });
  }

};
</script>

<style lang="scss" scoped>
