  width: 100%;
  height: 100%;
  background: #f5f5f5;
  .header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 99;
    width: 100%;
    height: 0.6rem;
    background: #0085ff;
    display: flex;
    justify-content: space-between;
    .header-icon-search {
      width: 20%;
      .search {

        color: #fff;
        font-size: 0.3rem;
        margin-left: 0.15rem;
        margin-top: 0.18rem;
      }
    }
    .address {
  
      width: 60%;
      // display: block;
      text-align: center;
