      text-overflow: ellipsis;
      color: #fff;
      span {
       
        font-size: 0.22rem;
        color: #fff;
        font-family: "圆体";
        line-height: 0.6rem;
       
        // font-weight: 600;
      }
    }
    .header-icon-user {
      width: 20%;  
      color: #fff;
      display: inline;
      line-height: 0.6rem;
      .el-icon-user {
        font-size: 0.3rem;
