      border-bottom: 1px #ccc solid;
      li {
        margin: 0.15rem 0;
        width: 25%;
        text-align: center;
        img {
          width: 0.6rem;
          height: 0.6rem;
          display: block;
          margin: 0.05rem auto;
        }
        span {
          font-size: 0.16rem;
