          line-height: 0.4rem;
          font-weight: 500;
        }
      }

      .main-content {
        margin-bottom: 0.6rem;
        width: 100%;
        ul {
          li {
            padding: 0.25rem 0;
            display: flex;
            border-bottom: #ccc 0.01rem solid;
            img {
              width: 0.9rem;
              height: 0.9rem;
              display: block;
              border: 0.01rem #ccc solid;
              
            }
            .item {
              flex-grow: 1;
              display: flex;
              justify-content: space-around;
              .item-left {
                float: left;
                .item-left-top {
                  display: flex;
                  span {
                    display: block;
                    height: 0.2rem;
                    font-weight: 600;
                    line-height: 0.2rem;
                    font-size: 0.14rem;
                    border-radius: 0.03rem;
                    padding: 0 0.02rem;
                    background: #ffd547;
                  }
                  p {
                    width: 1.2rem;
                    height: 0.18rem;
                    font-size: 0.18rem;
                    font-weight: 550;
                    line-height: 0.18rem;
                    margin-left: 0.05rem;
                    white-space: nowrap; /*一行显示*/
                    overflow: hidden; /*超出部分隐藏*/
                    text-overflow: ellipsis;
                  }
                }
                .item-left-content {
                  margin-top: 0.2rem;
                  display: flex;
                  font-size: 0.14rem;
                  color: #ccc;
                  span {
                    font-size: 0.14rem;
                    margin: 0 0.06rem;
                    line-height: 0.18rem;
                    color: #ffd547;
                  }
                  p {
                    font-size: 0.12rem;
                    color: #000;
                  }
                }
                .item-left-footer {
                  font-size: 0.13rem;
                  margin-top: 0.15rem;
                  color: #000;
                }
              }
              .item-right {
                float: right;
                font-size: 0.12rem;
                text-align: right;
                .item-right-top {
                  > span {
                    color: #999;
                    em {
                      font-style: normal;
                      margin: 0 0.05rem;
                    }
                  }
                }
                .item-right-content {
                  display: flex;
                  justify-content: flex-end;
                  margin-top: 0.25rem;
                  margin-bottom: 0.17rem;
                  span {
                    font-size: 0.1rem;
                    display: block;
                    color: #fff;
                    padding: 0 0.02rem;
                    border-radius: 0.02rem;
                    line-height: 0.16rem;
                    background: #398fdf;
                  }
                  p {
                    font-size: 0.1rem;
                    display: block;
                    padding: 0 0.02rem;
                    line-height: 0.16rem;
                    color: #398fdf;
                    border: 0.01rem solid #398fdf;
                    margin-left: 0.05rem;
                    border-radius: 0.02rem;
                  }
                }
                .item-right-footer {
                  span {
                    color: #999;
                    em {
                      font-style: normal;
