                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .footer {
    position: fixed;
    z-index: 99;
    bottom: 0;
    width: 100%;
    background: #fff;
    ul {
      border-top: 1px #ccc solid;
      display: flex;
      text-align: center;
      li {
        width: 25%;
        text-align: center;
        padding: 0.05rem 0;
        i {
          display: block;
          font-size: 0.35rem;
          color: #999;
        }
        span {
          color: #999;
          font-size: 0.16rem;
        }
      }
    }
  }
}
</style>